@import '~@yandex-music-int/sol/mixins/fontStyles';
@import '~@yandex-music-int/sol/mixins/breakpoints';
@import '~@yandex-music-int/sol/mixins/averageColorBackground.scss';

.root {
    // Почему здесь используется !important - смотри здесь https://st.yandex-team.ru/MFRONT-5960
    padding: 0 !important;
}

.averageColorBackground {
    @include averageColorBackground();
}

.header {
    padding-block-start: var(--ym-spacer-size-l);
    padding-block-end: var(--ym-spacer-size-xxxl);
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-xxxl);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-block-start: var(--ym-spacer-size-xxl);
        padding-block-end: var(--ym-spacer-size-xxl);
        padding-inline-start: var(--ym-spacer-size-l);
        padding-inline-end: var(--ym-spacer-size-l);
    }
}

.content {
    padding-inline-start: var(--ym-spacer-size-l);
    padding-inline-end: var(--ym-spacer-size-s);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-inline-start: var(--ym-spacer-size-s);
    }
}

.shimmerItem {
    height: 56px;
}

.footer {
    padding-block-start: calc(var(--ym-spacer-size-xxxl) * 2);
    padding-block-end: var(--ym-spacer-size-xxl);
    padding-inline-start: var(--ym-spacer-size-xxl);
    padding-inline-end: var(--ym-spacer-size-s);

    @include mediaBreakpointDown($ym-breakpoint-xxs, false) {
        padding-block-start: calc(var(--ym-spacer-size-xxl) * 2);
        padding-block-end: var(--ym-spacer-size-xxxl);
        padding-inline-start: var(--ym-spacer-size-l);
    }
}
